.newsCard {
    border: 1px black solid;
    border-radius: 10px;
    margin : 10px;
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
}


.infoCard {
    display: flex;
    justify-content: space-around;
    width: 50%;
    /* align-items: center; */
}

.newsLink {
    text-decoration : none;
    color: black;
}

.newsLinkButton:hover {
    cursor: pointer;
    box-shadow: 1px 1px 20px #ddd;
}